.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.na{width: 500px;margin:30px auto;background-color: darkslategray;height:70vh;padding: 20px;border-radius: 10px;overflow-y: scroll;overflow-x: scroll;}
.item{border-radius: 6px;}
.add{position: relative;}
.bi-file-plus-fill::before{font-size: 42px;position: absolute;top: -10px;left:-3px;}
.check{padding: 7px 20px;background-color: dodgerblue;color: white;text-decoration: none;border-radius: 7px;transition: 5s;
overflow: hidden;}
.check:hover .check2{display: none;padding: 7px 40px;padding: 0;}
.check:hover .check3{display: inline;color: white;transition: 5s;}
.check3{display: none;transition: 2s;}
